
import { defineComponent, ref } from "vue";
import { IonButton, IonCardSubtitle, IonCardTitle, IonCol, IonContent, IonGrid, IonIcon, IonInput, IonItem, IonRow } from "@ionic/vue";
import { person } from "ionicons/icons";
import LoginFooter from "@/components/LoginFooter.vue";
import useFacility from "@/composables/useFacility";

export default defineComponent({
  name: "DTLoginForm",
  components: {
    IonGrid,
    IonRow,
    IonCol,
    IonButton,
    IonInput,
    IonItem,
    IonCardTitle,
    IonCardSubtitle,
    LoginFooter,
    IonContent,
    IonIcon,
  },
  props: {
    showConfigBtn: {
      type: Boolean,
      default: false
    },
    version: {
      type: String,
      required: true,
    }
  },
  emits: ["login"],
  setup(_, { emit }) {
    const username = ref('')
    const password = ref('')
    const { facilityName, district } = useFacility();
    const login = () => emit("login", {username: username.value, password: password.value})

    return {
      avatar: person,
      username,
      password,
      facilityName,
      district,
      login
    }
  }
})
